import React, { useEffect } from 'react';
import './modal.css';

const Modal = ({children, active = false, closeModal}) => {

    return (
        <div onClick={closeModal} className={`modal ${active && 'modal-active'}`}>
            <div onClick={e => e.stopPropagation()} className="modal__content">
                {children}
            </div>
        </div>
    )
}

export default Modal;