import React, { useState } from 'react';
import { connect } from 'react-redux';
import './login.css'
import { singIn } from '../../redux/reducers/main-content.reducer';

const Login = ({ singIn }) => {

    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    const onChangeEmail = (e) => {
        setEmail(e.target.value)
    }

    const onChangePassword = (e) => {
        setPassword(e.target.value)
    }

    const onSubmit = (e) => {
        e.preventDefault();
        if(email && password) singIn(email, password);
    }

    return (
        <div className="login">
            <form className="login_form">
                <input required onChange={onChangeEmail} value={email} type="text" name="email" id="login-email" placeholder="email"/>
                <input required onChange={onChangePassword} value={password} type="text" name="password" id="login-password" placeholder="password"/>
                <button onClick={onSubmit} className='login_btn'>Войти</button>
            </form>
        </div>

    )
}

export default connect(null, {singIn})(Login);