import firebase from 'firebase/app';
import 'firebase/auth';
import 'firebase/firestore';
import 'firebase/storage';
// import 'firebase/database';


const firebaseAPI = {
    initialFirebase: async() => {
        firebase.initializeApp({
        apiKey: "AIzaSyBO9vJ8DZwZ0aQpoIXdA2f7aBgKCBmdmMs",
        authDomain: "socialapp-df184.firebaseapp.com",
        databaseURL: "https://socialapp-df184-default-rtdb.firebaseio.com",
        projectId: "socialapp-df184",
        storageBucket: "socialapp-df184.appspot.com",
        messagingSenderId: "487276857539",
        appId: "1:487276857539:web:3c1f4bd901a4dbee35ba76"
        });
    },

    //limit('2') orderBy('') where('feild', '==', 'value')
    getItems: async (category) => {
        if(category) {
            console.log('firebase call with category');
            const data = await firebase.firestore()
                .collection('items')
                .where('category', ('=='), category)
                .get()
                // .addOnSuccessListener(data => data)
                // .addOnFailureListener (error => {console.error(error)})
                
            return data.docs.map(doc => ({...doc.data(), id: doc.id}));

        }else{
            console.log('firebase call without category');
            const data = await firebase.firestore().collection('items').get();
            return data.docs.map(doc => ({...doc.data(), id: doc.id}));
        }

    },

    addItem: async (data) => {
        const iconName = data.icon.name;
        const ref = await firebase.storage().ref('icon/' + iconName);
        if(ref) await ref.put(data.icon);
        const iconUrl = await ref.getDownloadURL() || ''
        await firebase.firestore()
        .collection('items')    
        .add({...data, 
                icon: iconUrl,
                tags: data.tags.split(','),
                iconName: iconName || '',
                created: firebase.firestore.Timestamp.now().nanoseconds});
    },

    singOut: () => {
        firebase.auth().signOut()
    },
    
    singIn: async (email, password) => {
         await firebase.auth()
        .signInWithEmailAndPassword(email, password)
    },

    isAuth: async () => {
        let isAuth = false;
        await firebase.auth().onAuthStateChanged(user => {
            if(user) isAuth = true;
            else isAuth = false;
        })

        return isAuth;
    },

    removeItem: async (id, iconName) => {
        await firebase.firestore()
        .collection('items').doc(id).delete()
        await firebase.storage().ref('icon/' + iconName)
            .delete()
            .then(() => console.log('remove'))
            .catch(e => console.log('error remove: ', e))
    }
}

export default firebaseAPI;