import React, { useEffect } from 'react';
import { NavLink } from 'react-router-dom';
import PropTypes from 'prop-types';
import './tool-item.css';

const ToolItem = ({ id, icon, name, descr, img, link, category, tags, authStatus, removeItem, iconName }) => {
    return (
        <div className="tool-item">
            {authStatus && <div className="remove-btn" onClick={() => removeItem(id, iconName)}>✘</div>}
            <div className="tool-item__wrap-top">
                <div className="tool-item__category">
                    <NavLink to={'/category/'+ category}>{category}</NavLink>
                </div>
                <div className="tool-item__head">

                    <div className="tool-item__icon">
                        <img src={icon} alt="tool-icon" />
                    </div>
                    <div className="tool-item__title">
                        <h2>{name}</h2>
                    </div>
                </div>
                <div className="tool-item__descr">{descr}</div>
                <div className="tool-item__link">
                    <a className="tool-item__link-a" href={link}>{link} &gt;&gt;</a>
                </div>
            </div>
            <div className="tool-item__tags">
                {tags.map(tag => <span key={id + tag} className='tool-item__tags-item'>{tag}</span>)}
            </div>
        </div>
    )
}

ToolItem.propsTypes = {
    tags: PropTypes.array 
}

ToolItem.defaultProps = {
    tags: []
}

export default ToolItem;