// ACTION TYPES
const HIDE_MENU_STATUS = 'HIDE_MENU_STATUS';

//REDUCER
const initialState = {
    hideMenuStatus: true,
}

const menuReducer = (state = initialState, {type, payload}) => {
    switch(type){
        case HIDE_MENU_STATUS:
            return {
                ...state,
                hideMenuStatus: !state.hideMenuStatus
                
            }
        default: return state
    }
}

//ACTION CREATORS
export const setHideMenuStatus = () => {
    return {
        type: HIDE_MENU_STATUS
    }
}

export default menuReducer;