import { ErrorMessage, Field, Form, Formik } from 'formik';
import React from 'react';
import { connect } from 'react-redux';
import { addNewCard } from '../../redux/reducers/main-content.reducer';
import { getMenuCategoryList } from '../../redux/selectors/menuSelector';
import { FormikFile, FormikSelect, FormikTextarea, FormikTextInput } from '../formik/input-formik';


const newCardForm = ({addNewCard, category}) => {
    console.log('new card form');
    const initialFormValue = {
        category: category[0],
        name: '',
        descr: '',
        icon: '',
        img: '',
        link: '',
        tags: ''
    }

    return (
        <Formik
            initialValues={initialFormValue}
            onSubmit={(data, {setSubmitting, resetForm}) => {
                const formCallback = () => {
                    resetForm();
                    setSubmitting(false);
                }
                addNewCard(data, formCallback);
            }}
        >
            {({setFieldValue, isSubmitting}) => (
                <Form className='card_form'>

                    <FormikSelect name='category'>
                        {category.map(el => <option key={el} value={el}>{el}</option>)}
                    </FormikSelect>

                    <FormikTextInput
                        label="Title:"
                        name="name"
                        type="text"
                        placeholder=""
                    />
                    <FormikTextarea
                        label="Описание:"
                        name="descr"
                        type="text"
                        placeholder=""
                    />
                    <FormikTextInput
                        label="Ссылка:"
                        name="link"
                        type="text"
                        placeholder=""
                    />
                    <FormikTextInput
                        label="Tags (через запятую):"
                        name="tags"
                        type="text"
                        placeholder=""
                    />
                    <div className="card_form-row">
                        <FormikFile
                            label="Add Logo"
                            name="icon"
                            type="file"
                            onChange={(e) => {
                                setFieldValue('icon', e.target.files[0])
                            }}
                        />
                        <button type="submit" className='new-card-btn' disabled={isSubmitting}>Submit</button>
                    </div>
                </Form>

            )}
            
        </Formik>
    )
}

const mstp = (state) => ({
    category: getMenuCategoryList(state)
})

export default connect(mstp, {addNewCard})(newCardForm);

