import React from 'react';
import { connect } from 'react-redux';
import { NavLink } from 'react-router-dom';
import './header.css'
import { getAuthStatus } from '../../redux/selectors/toolItemSelectors';
import { setHideMenuStatus } from '../../redux/reducers/menuReducer';
import { logOut } from '../../redux/reducers/main-content.reducer';
import NewCard from '../new-card/new-card';


const Header = ({setHideMenuStatus, authStatus, logOut}) => {
    
    const adminPanelBtn = [
        <NewCard key='newCard'/>,
        <span key='logout' onClick={logOut}>Logout</span>
    ]

    return (
        <div className="header">
            <div className="logo">
                <NavLink to='/'><h2>AllTools</h2></NavLink>
                {/* <img src="" alt="logo"/> */}
            </div>
            <nav >
                <div className="menu">
                    {authStatus && adminPanelBtn}
                    <span onClick={setHideMenuStatus}>Категории</span>
                </div>
            </nav>
        </div>
    )
}

const mstp = (state) => {
    return {
        authStatus: getAuthStatus(state)
    }
}

const msdp = {
    setHideMenuStatus,
    logOut
}

export default connect(mstp, msdp)(Header); 
                                 
                            