import { createSelector } from "reselect";

export const getToolItems = (state) => {
    return state.toolItems.items;
}

export const getLoadingStatus = (state) => {
return state.toolItems.isLoading;
}

export const getInitialStatus = (state) => {
    return state.toolItems.initialStatus
}

export const getAuthStatus = (state) => {
    return state.toolItems.authStatus
}

