import { useEffect } from 'react';
import { Route, Switch } from 'react-router-dom';
import './App.css';
import Header from './components/header/header';
import MainContentContainer from './components/main-content/main-content.container';
import PopUpMenu from './components/popup-menu/pop-up-menu';
import firebaseAPI from './API/firebaseAPI';
import { initializeState } from './redux/reducers/main-content.reducer';
import { connect } from 'react-redux';
import { getLoadingStatus, getInitialStatus } from './redux/selectors/toolItemSelectors';
import Loader from './components/common/loader';
import AdminPanel from './components/admin/admin';
import NewCard from './components/new-card/new-card';

const App = ({initializeState, initialStatus}) => {
  
  useEffect(() => {
    initializeState();
  }, [])

  if (initialStatus) return <Loader />
  console.log('APP UPDATE');
  return (
    <div className="App">
      <Header/>
      <PopUpMenu/>
      <Switch>
        <Route path='/category/:category?' component={MainContentContainer}/>
        <Route exact path='/' component={MainContentContainer}/>
      </Switch>
        <Route exact path='/admin' component={AdminPanel} />
        <Route exact path='/new-card' component={NewCard} />
    </div>
  )
}

const mapStateToProps = (state) => {
  return {
    initialStatus: getInitialStatus(state)
  }
}

export default connect(mapStateToProps, {initializeState})(App);
