import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { getToolItems, getLoadingStatus, getAuthStatus } from '../../redux/selectors/toolItemSelectors';
import MainContent from './main-content';
import { setToolItems, removeItem} from '../../redux/reducers/main-content.reducer';
import firebaseAPI from '../../API/firebaseAPI';
import Loader from '../common/loader';

const MainContentContainer = ({setToolItems, isLoading, ...props}) => {   
   
    useEffect(() => {
        setToolItems(props.match.params.category)
    },[props.match.params.category])

    if(isLoading) return <Loader />

    return <MainContent {...props}/>
}

const mapStateToProps = (state) => {
    return {
        items: getToolItems(state),
        isLoading: getLoadingStatus(state),
        authStatus: getAuthStatus(state),

    }
}

const mapDispatchToProps = {
    setToolItems,
    removeItem
}

export default connect(mapStateToProps, mapDispatchToProps)(MainContentContainer);