import firebaseAPI from '../../API/firebaseAPI';

//ACTION TYPES
const LOADING_STATUS = 'LOADING_STATUS';
const SET_TOOL_ITEMS = 'SET_TOOL_ITEMS'; 
const INITIALIZE_STATE = 'INITIALIZE_STATE';
const SET_CATEGORY_LIST = 'SET_CATEGORY_LIST';
const SET_INITIAL_APP_STATUS = 'SET_INITIAL_APP_STATUS';
const SET_CURRENT_CATEGORY = 'SET_CURRENT_CATEGORY';
const SET_AUTH_STATUS = 'SET_AUTH_STATUS';

//INITIAL STATE
const initialState = {
    items: [],
    isLoading: false,
    category: [],
    initialStatus: true,
    authStatus: false,
    addNewCardStatus: false,
}

//REDUCER
const toolItems = (state = initialState, {type, payload}) => {
    switch(type){
        case LOADING_STATUS:
            return {
                ...state,
                isLoading: payload
            }
        
        case SET_TOOL_ITEMS:
            return {
                ...state,
                items: [...payload]
            }

        case INITIALIZE_STATE:
            return {
                ...state,
                items: [...payload],
            }
        case SET_CATEGORY_LIST:
            return {
                ...state,
                category: [...payload]
            }
        case SET_INITIAL_APP_STATUS: 
            return {
                ...state,
                initialStatus: payload
            }
            
        case SET_CURRENT_CATEGORY: 
            return state

        case SET_AUTH_STATUS: 
            return {
                ...state,
                authStatus: payload
            }

        default: return state;
    }
}

export default toolItems;

// ACTION CREATORS
const setLoadingStatus = (payload) => {
    return {
        type: LOADING_STATUS,
        payload
    }
}

const setToolItemsAC = (payload) => {
    return {
        type: SET_TOOL_ITEMS,
        payload
    }
}

const initializeStateAC = (payload) => {
    return {
        type: INITIALIZE_STATE,
        payload
    }
}

const setCategoryListAC = (payload) => {
    return {
        type: SET_CATEGORY_LIST,
        payload
    }
}

const setInitialStatus = (payload) => {
    return {
        type: SET_INITIAL_APP_STATUS,
        payload
    }
}

const setCurrentCategoryAC = (payload) => {
    return {
        type: SET_CURRENT_CATEGORY,
        payload
    }
}

const setAuthStatusAC = (payload) => {
    return {
        type: SET_AUTH_STATUS,
        payload
    }
}

const singInAC = () => {

}

//THUNKS
export const setToolItems = (category) => async (dispatch) => {
    dispatch(setLoadingStatus(true));
    const data = await firebaseAPI.getItems(category);
    // dispatch(setCurrentCategoryAC(category));
    dispatch(setToolItemsAC(data));
    dispatch(setLoadingStatus(false));
}

export const initializeState = () => async(dispatch) => {
    dispatch(setInitialStatus(true));                           // start set status
    
    const data = await firebaseAPI.getItems();                  // fetch data
    dispatch(initializeStateAC(data));                          // set fetch data to state

    const categoryList = data.map(({category}) => category);    // get category list
    const uniqueCategoryList = new Set(categoryList);           // get unique category list
    dispatch(setCategoryListAC(uniqueCategoryList));            // set unique category list to state

    const isAuth = await firebaseAPI.isAuth();                  // set auth status to state
    dispatch(setAuthStatusAC(isAuth));

    dispatch(setInitialStatus(false));
}

export const singIn = (email, password) => async (dispatch) => {
    await firebaseAPI.singIn(email, password);
    const isAuth = await firebaseAPI.isAuth();                  // set auth status to state
    dispatch(setAuthStatusAC(isAuth));
}

export const logOut = () => async (dispatch) => {
    const {singOut, isAuth} = firebaseAPI;
    await singOut();
    const authStatus = await isAuth();
    dispatch(setAuthStatusAC(authStatus));
}

export const addNewCard = (data, formCallback) => async (dispatch) => {
    await firebaseAPI.addItem(data);
    await dispatch(setToolItems());
    formCallback();
    console.log('add new card response: ');
}

export const removeItem = (id, iconName) => async (dispatch) => {
    await firebaseAPI.removeItem(id, iconName);
    await dispatch(setToolItems());
}

