import React, { useState } from 'react';
import './new-card.css';
import Modal from '../modal/modal';
import NewCardForm from './new-card-form-formik';

const NewCard = () => {

  const [active, setActive] = useState(false)
  const closeModal = () => {
    setActive(!active);
  }

  //if auth false
  return (
    <>
      <span onClick={() => {setActive(!active)}} key='new-card'>New Card</span>

      <Modal active={active} closeModal={closeModal}>
          <NewCardForm />
      </Modal>
    </>
  )
}

export default NewCard;
