import React from 'react';
import { NavLink } from 'react-router-dom';
import './pop-up-menu.css';
import { connect } from 'react-redux';
import { setHideMenuStatus } from '../../redux/reducers/menuReducer';
import { getToolItems } from '../../redux/selectors/toolItemSelectors';
import { getMenuStatus, getMenuCategoryList } from '../../redux/selectors/menuSelector';


const PopUpMenu = ({setHideMenuStatus, hideMenuStatus, categoryList}) => {
    
    const categoryListData = categoryList.map(category => {
        return  <NavLink key={category} to={'/category/'+ category}> <li >{category}</li> </NavLink>
    })

    return (
        <div onMouseLeave={setHideMenuStatus} className={hideMenuStatus ? 'pop-up-menu__wrapp pop-up-menu--hidden' : 'pop-up-menu__wrapp'}>
            <div className="pop-up-menu">
                <ul className="pop-up-menu__inner">
                    <NavLink key='all' to={'/'}> <li >Все</li> </NavLink>
                    {categoryListData}
                </ul>
            </div>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        hideMenuStatus: getMenuStatus(state),
        categoryList: getMenuCategoryList(state)
    };
}

export default connect(mapStateToProps, {setHideMenuStatus})(PopUpMenu)