import React from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router';
import firebaseAPI from '../../API/firebaseAPI';
import { getAuthStatus } from '../../redux/selectors/toolItemSelectors';
import Login from '../login/login';
import './admin.css'

const AdminPanel = ({authStatus}) => {
    return !authStatus ? <Login /> : <Redirect to='/'/>
}

const mstp = (state) => {
    return {
        authStatus: getAuthStatus(state)
    }
}

export default connect(mstp)(AdminPanel);